@import 'roots';

article {
    p, ul {
    text-align: justify;
    hyphens: auto;
    }
}
img.inline.float-right {
    margin: 0 0 1rem 2rem;
    max-width: 50%;
    object-fit: cover;
}

img.inline.float-left {
    margin: 0 2rem 1rem 0;
    max-width: 50%;
    object-fit: cover;
}

article ul li {
    font-size: 1.25rem;
    font-weight: 300;
    &:not(:last-of-type) {
        @apply mb-4;
    }
}

#maps {
    transition: $transition;
    &:hover {
        color: $theme;
    }
    &::before {
        content: '';
        mask: url('images/marker.svg');
        background: $dark;
        mask-size: contain;
        mask-repeat: no-repeat;
        width: 15px;
        height: 15px;
        display: inline-block;
        transition: $transition;
    }
    &:hover::before {
        background: $theme;
    }
}